<template>
  <div class="table__img-link">
    <img v-for="image in images"
        class=""
        alt="404"
        :src="image"
        @click="() => showImg(image)"
    >
    <vue-easy-lightbox
        :visible="visible"
        :imgs="imgs"
        @hide="visible = false"
    />
  </div>
</template>

<script>
import {ref} from "vue";

export default {
  name: 'light-box',
  props: {
    images: Array,
  },
  setup(props) {
    const imgs = ref('');
    const visible = ref(false);
    return {
      visible,
      imgs,
      showImg(url) {
        this.imgs = url
        this.visible = true
      }
    }
  },
}
</script>

